import observerIsSupported from './helpers/support'
import getOffsets from './helpers/offset'

const initialize = function (target, once, inViewCallback, offset, outViewCallback) {
  if (!inViewCallback && !outViewCallback) {
    return
  }
  let observer
  let boundingRect
  let isUpdating = false
  let isVisible = false
  const offsets = getOffsets(offset)

  const setOutView = function () {
    if (outViewCallback && isVisible) {
      try {
        outViewCallback()
      } catch (err) {
        console.error(err)
      }
    }
    isVisible = false
  }

  const setInView = function (entry) {
    if (inViewCallback && !isVisible) {
      try {
        inViewCallback()
      } catch (err) {
        console.error(err)
      }
      if (once) {
        removeObserver(entry)
      }
    }
    isVisible = true
  }

  const removeObserver = function (entry) {
    if (observerIsSupported()) {
      observer.unobserve(entry.target)
    } else {
      window.removeEventListener('scroll', _scrollHandler, {capture: false, passive: true})
    }
  }

  const _scrollUpdate = function () {
    boundingRect = target.getBoundingClientRect()
    var topBorderOfElementIsInViewport = boundingRect.top <= (window.innerHeight + offsets.bottom)
    var bottomBorderOfElementIsInViewport = (boundingRect.bottom - offsets.bottom) >= 0
    if (topBorderOfElementIsInViewport && bottomBorderOfElementIsInViewport) {
      setInView()
    } else {
      setOutView()
    }
    isUpdating = false
  }

  const _scrollHandler = function () {
    if (isUpdating) {
      return false
    }
    if (window.requestAnimationFrame) {
      window.requestAnimationFrame(_scrollUpdate)
      isUpdating = true
    } else {
      _scrollUpdate()
    }
  }

  if (observerIsSupported()) {
    const config = {
      rootMargin: offsets.top + 'px ' + offsets.right + 'px ' + offsets.bottom + 'px ' + offsets.left + 'px',
    }
    observer = new IntersectionObserver(function (entries) {
      entries.forEach(function (entry) {
        if (entry.isIntersecting) {
          setInView(entry)
        } else {
          setOutView()
        }
      })
    }, config)
    observer.observe(target)
  } else {
    window.addEventListener('scroll', _scrollHandler, {capture: false, passive: true})
  }
}

export default initialize
