/**
 * Add IntersectionObserver to observe elements entering and leaving the viewport
 * Fallback: scroll event with requestAnimationFrame
 * @param  {object} target         - Target node element to observe
 * @param  {boolean} once    - If true: set unobserve
 * @param  {function} inViewCallback  - Function to call target element enters viewport
 * @param  {integer} offset    - Offset to specify when to call callbacks
 * @param  {function} outViewCallback  - Function to call target element leaves viewport
 */

import initialize from './initialize.js'

const once = function (target, inViewCallback, offset, outViewCallback) {
  initialize(target, true, inViewCallback, offset, outViewCallback)
}

const repeat = function (target, inViewCallback, offset, outViewCallback) {
  initialize(target, false, inViewCallback, offset, outViewCallback)
}

export default {
  once,
  repeat,
}
