const getOffsets = function (offset) {
  let offsets = {
    top: 0,
    right: 0,
    bottom: 0,
    left: 0,
  }
  if (!offset) {
    return offsets
  }
  if (typeof offset === 'object') {
    offsets.top = offset.top || 0
    offsets.right = offset.right || 0
    offsets.bottom = offset.bottom || 0
    offsets.left = offset.left || 0
  } else {
    offsets.bottom = offset
  }
  return offsets
}

export default getOffsets
